import React, { Fragment } from "react";
import imagen1 from "../background.jpg";
import cripto from "../criptoPage.PNG";
// import blackjack from "../blackjack.PNG";
// import platzi from "../platzi.PNG";
import loopstudios from "../loopstudios.PNG";
import countriesapi from "../countriesapi.PNG";
import designo from "../designo.PNG";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub } from "@fortawesome/free-brands-svg-icons";

import { faGlobe } from "@fortawesome/free-solid-svg-icons";
import ScrollAnimation from "react-animate-on-scroll";

const Projects = () => {
  return (
    <>
      <div class="bg-white pt-5">
        <div className="container " id="projects">
          <div className="row">
            <div className="col text-center ">
              <h2>Projects</h2>
            </div>
          </div>
          <ScrollAnimation animateIn="fadeOut">
            <div className="row">
              <div className="col-12 col-md-6 col-lg-4 mb-4 ">
                <div className="card  ">
                  <div className="overflow">
                    <a
                      href="https://felixhiguera.github.io/mobileFirst/"
                      target="_blank"
                    >
                      <img
                        src={cripto}
                        className="card-img-top"
                        alt="cripto page"
                      />
                    </a>
                  </div>
                  <div className="card-body">
                    <div class="badges mb-2">
                      <span class="badge bg-secondary">HTML</span>

                      <span class="badge bg-primary">CSS</span>
                      <span class="badge bg-warning">Javascript</span>
                    </div>
                    <h5 className="card-title">Cryptocurrency Website</h5>
                    <p className="card-text">
                      This modern and appealing website was built with ‘’Mobile
                      First’’ in mind. The main techniques are HTML5 and CSS3.
                      The branding matches this project s requirements.
                    </p>
                    <a
                      href="https://felixhiguera.github.io/mobileFirst/"
                      target="_blank"
                    >
                      <FontAwesomeIcon icon={faGlobe} size="2x" color="blue" />
                    </a>
                    <a
                      href="https://github.com/felixhiguera/mobileFirst"
                      target="_blank"
                    >
                      <FontAwesomeIcon
                        icon={faGithub}
                        size="2x"
                        color="black"
                      />
                    </a>
                  </div>
                </div>
              </div>
              <div className="col-12 col-md-6 col-lg-4 mb-4">
                <div className="card ">
                  <div className="overflow">
                    <a
                      href="https://felixhiguera.github.io/loopstudios/"
                      target="_blank"
                    >
                      <img
                        src={loopstudios}
                        className="card-img-top"
                        alt="Loopstudios"
                      />
                    </a>
                  </div>
                  <div className="card-body">
                    <div class="badges mb-2">
                      <span class="badge bg-secondary">HTML</span>

                      <span class="badge bg-primary">CSS</span>
                    </div>
                    <h5 className="card-title">Loopstudios Website</h5>
                    <p className="card-text">
                      Loopstudios website is a modern responsive website made
                      with HTML5, CSS, Bootstrap, it was used CSS Grid for the
                      cards and for making responsive was used Media.
                    </p>
                    <a
                      href="https://felixhiguera.github.io/loopstudios/"
                      target="_blank"
                    >
                      <FontAwesomeIcon icon={faGlobe} size="2x" color="blue" />
                    </a>
                    <a
                      href="https://github.com/felixhiguera/loopstudios"
                      target="_blank"
                    >
                      <FontAwesomeIcon
                        icon={faGithub}
                        size="2x"
                        color="black"
                      />
                    </a>
                  </div>
                </div>
              </div>
              <div className="col-12 col-md-6 col-lg-4 mb-4">
                <div className="card">
                  <div className="overflow">
                    <a
                      href="https://felixhiguera.github.io/designo/#/"
                      target="_blank"
                    >
                      <img
                        src={designo}
                        className="card-img-top "
                        alt="Designo website"
                      />
                    </a>
                  </div>
                  <div className="card-body">
                    <span class="badge bg-secondary">HTML</span>

                    <span class="badge bg-primary">REACT</span>
                    <span class="badge bg-light">CSS</span>
                    <span class="badge bg-warning">Javascript</span>

                    <h5 className="card-title">Designo Website</h5>
                    <p className="card-text" id="contactMe">
                      Designo website is a modern responsive website (7 page
                      site). It includes authentication form for email and empty
                      inputs in the Contact page.
                    </p>
                    <a
                      href="https://felixhiguera.github.io/designo/#/"
                      target="_blank"
                    >
                      <FontAwesomeIcon icon={faGlobe} size="2x" color="blue" />
                    </a>
                    <a
                      href="https://github.com/felixhiguera/designo"
                      target="_blank"
                    >
                      <FontAwesomeIcon
                        icon={faGithub}
                        size="2x"
                        color="black"
                      />
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </ScrollAnimation>
        </div>
      </div>
    </>
  );
};

export default Projects;
